
.no-wrap {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.extend-click {
  position: relative;
}
.extend-click:before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}
.m-header {
  position: relative;
  height: 44px;
  text-align: center;
  color: #ffcd32;
  font-size: 0;
}
.m-header .icon {
  display: inline-block;
  margin-top: 6px;
  margin-right: 9px;
  width: 30px;
  height: 32px;
  vertical-align: top;
  background-image: url(logo@2x.png);
  background-size: 30px 32px;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.m-header .icon {
    background-image: url(logo@3x.png);
}
}
.m-header .text {
  display: inline-block;
  vertical-align: top;
  line-height: 44px;
  font-size: 18px;
}
.m-header .mine {
  position: absolute;
  top: 0;
  right: 0;
}
.m-header .mine .icon-mine {
  display: block;
  padding: 12px;
  font-size: 20px;
  color: #ffcd32;
}



